.page-img {
  background-image: url("./img/cover2.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  height: 650px;
}

.App .header-img {
  background-color: #ededed;
  position: absolute;
  right: 0;
  left: 0;
}

.header-img .link {
  border: 1px solid #20599a;
  border-radius: 5px;
  display: inline-block;
  width: 180px;
  height: 40px;
  margin-top: 20px;
  margin-right: 10px;
  background-color: white;
  padding-top: 5px;
  cursor: pointer;
}

.header-img .link:hover {
  opacity: 0.7;
}

.header-img .link .fas {
  margin-right: 10px;
  color: #20599a;
}

.header-img .link .insert-vaction-link {
  color: #20599a;
}

.header {
  display: flex;
  justify-content: space-between;
  line-height: 100px;
}

.header img {
  width: 100px;
  height: 100px;
}

.header .header-links>a {
  margin-right: 20px;
}

.header .header-links .username-hello {
  margin-right: 20px;
}

.Login .login-form {
  width: 300px;
  height: 300px;
  display: inline-block;
  margin-top: 150px;
  border: 1px solid #20599a;
  border-radius: 5px;
  padding: 8px;
  background-color: #ffffff;
}

.Login .login-form #login-title {
  color: #20599a;
  margin-top: 10px;
}

.registration {
  padding: 40px;
}

.registration .Registration-form {
  max-width: 400px;
  height: 450px;
  background-color: #ffffff;
  /* margin: 40px auto; */
  border: 1px solid #20599a;
  border-radius: 5px;
  padding: 8px;
  margin: auto;
}

.registration .Registration-form #emailHelp {
  display: flex;
}

.registration .Registration-form #Registration-title {
  color: #20599a;
  margin-top: 10px;
}

.addVacation .Registration-form {
  max-width: 800px;
  /* height: 920px; */
  background-color: #ffffff;
  margin: 15px auto;
  border: 1px solid #20599a;
  border-radius: 5px;
  padding: 30px;
}

.addVacation .Registration-form h4 {
  color: #20599a;
}

.addVacation .Registration-form label {
  display: flex;
  color: #20599a;
}

.addVacation .Registration-form img {
  width: 100%;
}

.addVacation .Registration-form button {
  width: 100%;
}

.addVacation .Registration-form .all-vacation-link {
  margin-top: 20px;
  color: #20599a;
}

.Vacation .card {
  width: 300px;
  min-height: 400px;
  margin-left: 20px;
  margin-top: 20px;
  border: 1px solid #20599a;
  border-radius: 5px;
}

.Vacation .card .delete-edit {
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 5px;
}

.Vacation .card .vacation-id {
  position: absolute;
  left: 5px;
  top: 5px;
  background: #ffffff;
  padding: 3px 8px;
  border-radius: 20px;
  border: 1px solid #20599a;
  font-size: 12px;
  font-weight: bold;
}

.Vacation .card i {
  margin-right: 10px;
}

.Vacation .card .card-img-top {
  height: 150px;
  width: 298px;
}

.Vacation .card .card-title {
  color: #20599a;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Vacation .card .card-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Vacation .card .card-text:hover {
  overflow: visible;
  white-space: pre-wrap;
}

.Vacation .card .card-body-edit {
  padding: 5px;
}

.Vacation .card .update-button .save-button {
  color: #20599a;
  border: 1px solid #20599a;
  border-radius: 5px;
  width: 100px;
  margin-right: 10px;
  margin-bottom: 10px;
  cursor: pointer;
}

.Vacation .card .update-button .save-button:hover {
  opacity: 0.7;
}

.Vacation .card .update-button .cancel-button {
  color: #20599a;
  border: 1px solid #20599a;
  border-radius: 5px;
  width: 100px;
  margin-bottom: 10px;
  cursor: pointer;
}

.Vacation .card .update-button .cancel-button:hover {
  opacity: 0.7;
}

.Vacation .card .follow {
  border: 1px solid #20599a;
  border-radius: 5px;
  width: 110px;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  /* margin-top: 20px; */
  margin-bottom: 20px;
}

.Vacation .card .follow:hover {
  opacity: 0.7;
}

.Vacation .card .follow .follow-button {
  color: #20599a;
}

.AllVacations {
  /* display: flex; */
  /* flex-wrap: wrap; */
  /* flex-direction: row; */
  margin-top: 30px;
  /* justify-content: center; */
}

.AllVacations .title {
  text-align: center;
  color: #20599a;
  font-family: cursive, Arial, Helvetica, sans-serif;
}

.AllVacations h3 {
  text-align: left;
  margin-left: 10px;
  margin-top: 30px;
}

