/* Graph */

.graph h3 {
  color: #20599a;
  margin-top: 10px;
}

.recharts-wrapper {
  margin-left: auto;
  margin-right: auto;
}

.recharts-wrapper .recharts-surface {
  background-color: #ffffff;
}

.graph .link-graph {
  border: 1px solid #20599a;
  border-radius: 5px;
  display: inline-block;
  width: 180px;
  height: 40px;
  margin-top: 20px;
  margin-right: 10px;
  background-color: white;
  padding-top: 5px;
  cursor: pointer;
}

.graph .link-graph:hover {
  opacity: 0.7;
}

.graph .link-graph .graph-vaction-link {
  color: #20599a;
}